body {
  margin: 0;
  font-family: monospace, fantasy;
}

.container {
  min-height: 100vh;
  background-color: #e0ffff;
}

.header {
  padding: 10px;
  display: flex;
}

.search-container {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  display: flex;
}

.search-container > input {
  width: 50%;
  border: 2px solid #d95959;
  padding: 10px;
}

.search-container > p {
  width: 50%;
  color: red;
}

.table-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.checkbox {
  accent-color: red;
  width: 20px;
  height: 18px;
}

table {
  width: 80%;
}

tr {
  border: 2px solid #000;
}

td {
  text-align: center;
  padding: 10px;
}

.action-icon {
  width: 15px;
  cursor: pointer;
  padding: 3px;
}

.pagination {
  width: 70%;
  justify-content: space-between;
  margin: 15px;
  padding: 15px;
  display: flex;
}

.delete-btn {
  width: 12%;
  cursor: pointer;
  background-color: #faebd7;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination-btns-container {
  width: 80%;
  justify-content: space-around;
  display: flex;
}

.arrow-btns {
  width: 30px;
  cursor: pointer;
}

.page-btn {
  cursor: pointer;
  padding: 10px;
}

.active-page {
  color: #fff;
  cursor: none;
  background-color: red;
}

.modal-container {
  min-width: 100vw;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
}

.modal-content {
  min-width: 31%;
  min-height: 28%;
  opacity: .9;
  background: #6495ed;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 14%;
}

.modal-content > img {
  width: 34px;
  position: absolute;
  top: -17px;
  right: -18px;
}

.update-form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.input-field-container {
  width: 100%;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
  display: flex;
}

.input-field-container > label {
  margin-right: 10px;
}

.input-field-container > input, select {
  width: 74%;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 10px 20px;
}

.submit-btn {
  background-color: #adff2f;
  border: 0;
  border-radius: 8px;
  padding: 8px;
}

.error-container {
  justify-content: center;
  display: flex;
}

@media screen and (max-width: 600px) {
  body {
    font-size: 12px;
  }

  table {
    width: 100%;
  }

  .checkbox {
    width: 12px;
  }

  td {
    padding: 4px;
  }

  .pagination {
    width: 90%;
  }

  .delete-btn {
    width: 15%;
  }

  .arrow-btns {
    width: 13px;
  }

  .page-btn {
    padding: 5px;
  }

  .action-icon {
    width: 12px;
  }
}

